.PanelHeader {
  background: transparent !important;
}

.stage {
  bottom: 0 !important;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden ;
  background: steelblue ;
  box-shadow: 0 -400px 300px -300px orangered inset;
  animation: sky-change 15s infinite linear ;
  }
@keyframes sky-change {
  0% {
    background: darkslateblue;
  }
  50% {
    background: skyblue;
    box-shadow: 0 -400px 300px -300px steelblue inset;
  }
  85% {
    background: darkslateblue;
    box-shadow: 0 -400px 300px -300px orangered inset;
  }
  100% {
    background: darkslateblue;
  }
}
.stageSucces {
  bottom: 0 !important;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden ;
background-color: #F4D03F;
background-image: linear-gradient(132deg, #F4D03F 0%, #16A085 100%);


  animation: sky-change-succes 15s infinite linear ;
  }

.stageError {
  bottom: 0 !important;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden ;
background-color: #FFE53B;
background-image: linear-gradient(147deg, #FFE53B 0%, #f50000 46%);
  animation: sky-change-error 3s ;
  }

.sun {
  position: absolute;
  left: 0;
  bottom: -30%;
  width: 10em;
  height: 10em;
  margin-left: -2.5em;
  margin-top: 0;
  background: orangered;
  border-radius: 50%;
  box-shadow: 0 0 20px orange;
  animation: 15s infinite linear;
  animation-name: sun-move, sun-color;
  }

.grass {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: url(../img/new-york.png) no-repeat center bottom;
}

@keyframes sun-move {
  0% {
  }
  10% {
    margin-left: -4.5em;
  }
  20% {
    margin-left: -5.5em;
  }
  30% {
    margin-left: -6em;
  }
  40% {
    margin-left: -4.5em;
  }
  50% {
    left: 50%;
    bottom: 75%;
    width: 5em;
    height: 5em;
    margin-left: -2.5em;
  }
  60% {
    margin-left: 1.5em;
  }
  70% {
    margin-left: 3.5em;
  }
  80% {
    margin-left: 2.5em;
  }
  90% {
    margin-left: 1em;
  }
  100% {
    left: 98%;
    margin-left: -2.5em;
  }
}

@keyframes sun-color {
  20% {
    background: orange;
  }
  40% {
    background: gold;
    box-shadow: 0 0 35px gold;
  }
  60% {
    background: yellow;
    box-shadow: 0 0 35px yellow;
  }
  80% {
  background: orange;
  }
}


 
@keyframes rotate {
  0% { background-position: 0px; }
  100% { background-position: 190px; }
}
